  .cabeçalho{
    background: #333;
    border-bottom: solid 5px #4faddb;
    box-sizing: content-box;
    height: 80px;
  }
  .logo{
    display: inline-block;
    height: 80px;
    padding: 15px;
  }
  .logo img{
    height: 100%;
  }
  .menu-toggle{
    float: right;
    background-color: #555;
    border: solid 1px #777;
    border-radius: 5px;
    height: 40px;
    padding: 10px 20px;
    margin: 20px 15px;
  }
  .menu{
    display: inline-block;
    vertical-align: top;
    height: 80px;
    padding: 25px;
  }
  .menu ul{
    margin: 0;
    padding: 0;
  }
  .menu li{
    display: inline-block;
    margin-right: 10px;
  }
  .cabeçalho a{
    text-decoration: none;
    color: #eee;
    font-size: 1.3em;
    /* padding: 15px; */
  }
  .autenticacao{
    float: right;
    height: 80px;
    padding: 25px;
  }
  .menu a:hover,
  .menu a.selected{
      background-color: #4faddb;
      color: #000;
  }
  .botao{
    border-radius: 30px;
    padding: 10px 25px;
    margin: 10px;
  }
  .destaque{
    background-color: #f50a31;
    color: #fff;
  }
  @media(min-width: 679px){
    .menu-toggle{
      display: none;
    }
  }
  @media(max-width: 680px){
    .menu{
      display: none;
    }
  }
  @media(max-width: 910px){
    .autenticacao{
      display: none;
    }
  }